
<style scoped>
.tag_area {
    border-bottom: 1px solid rgb(238, 238, 238);
    border-top: 1px solid rgb(238, 238, 238);
    padding: 10px 0;
}
</style>


<template>
    <div>
        <el-card>
            <el-button type='primary' @click="open_role_adder=true">添加角色</el-button>
            <el-table
                v-if="role_list"
                :data='role_list'
            >
                <el-table-column type='expand' >
                    <template slot-scope="role_level" v-if="role_list">
                        <el-row
                            class="tag_area"
                            v-for='(manage_level,manage_index) in role_level.row.children' :key='manage_index'
                        >
                            <el-col :span="5">
                                <el-tag 
                                    closable
                                >{{ manage_level.authName }}</el-tag>
                                <el-icon class="el-icon-caret-right"></el-icon>
                            </el-col>
                            <el-col :span="19">
                                <el-row >
                                    <el-col :span="6">
                                        <el-tag closable
                                            type='success'
                                        >{{ '示例' }}</el-tag>
                                        <el-icon class="el-icon-caret-right"></el-icon>
                                    </el-col>
                                    <el-col :span="18">
                                        <el-tag closable
                                            type='warning'
                                            v-for='(list_level,list_index) in manage_level.children' :key='list_index'
                                        >{{ list_level.authName }}</el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                    </template>
                </el-table-column>
                <el-table-column prop='id' label="#" />
                <el-table-column prop='roleName' label="角色名称" />
                <el-table-column prop='roleDesc' label="角色描述" />
                <el-table-column prop='option' label="操作" >
                    <div slot-scope="scoped">
                        <el-button type='primary' icon="el-icon-edit" @click='onfatechange(scoped.row,"edit")'>编辑</el-button>
                        <el-button type='danger' icon='el-icon-delete' @click='onfatechange(scoped.row,"del")'>删除</el-button>
                        <el-button type='warning' icon="el-icon-setting" @click='onfatechange(scoped.row,"set")'>分配权限</el-button>
                    </div>
                </el-table-column>
            </el-table>

        </el-card>

        <!-- 添加角色 弹层 -->
        <el-dialog 
            title="添加角色"
            :visible='open_role_adder'
        >
            <div>
                <el-input v-model="newerinfo.name" required>
                    <div slot="prepend">角色名称</div>
                </el-input>
                <el-input v-model="newerinfo.desc" required>
                    <div slot="prepend">角色描述</div>
                </el-input>
            </div>
            <div slot="footer">
                <el-button @click="open_role_adder=false">取消</el-button>
                <el-button type='primary' @click="waytonew">确定</el-button>
            </div>
        </el-dialog>

        <!-- 修改用户信息 面板 -->
        <el-dialog title="修改用户信息"
            :visible='show_edit_board'
        >
            <div>
                <el-input v-model="editorinfo.name" required>
                    <div slot="prepend">角色名称</div>
                </el-input>
                <el-input v-model="editorinfo.desc" required>
                    <div slot="prepend">角色描述</div>
                </el-input>
            </div>
            <div slot="footer">
                <el-button @click="show_edit_board=false">取消</el-button>
                <el-button type='primary' @click="waytoedit(r)">确定</el-button>
            </div>
        </el-dialog>
        <!-- 删除用户面板 -->
        <el-dialog title="删除用户"
            :visible='show_del_board'
        >
            <div slot="footer">
                <el-button @click="show_del_board=false">取消</el-button>
                <el-button type='primary' @click="waytodel(r)" >确定</el-button>
            </div>
        </el-dialog>

        <!-- 分配角色面板 -->
        <el-dialog title="分配角色"
            :visible='show_role_board'
        >
            <div v-if='tagtree'>
                <el-tree
                    ref="tagtree"
                    :data='tagtree'
                    show-checkbox
                    node-key="id"
                    :default-expanded-keys='tags_extended'
                    :default-checked-keys='tags_checked'
                    @check-change='when_marked_change'
                ></el-tree>
            </div>
            <div slot="footer">
                <el-button @click="show_role_board=false">取消</el-button>
                <el-button type='primary' @click="waytorole(r)">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>


<script>
export default {
    name:'rolelist',
    data() {
        return {
            // 数据列表
            role_list:null,
            // 记录活跃项信息
            r:null,
            // 添加用户 开关
            open_role_adder:false,
            // 编辑面板 开关
            show_edit_board:false,
            // 删除面板 开关
            show_del_board:false,
            // 分配角色 面板 开关
            show_role_board:false,
            // 新添角色信息
            newerinfo:{
                name:'',
                desc:'',
            },
            // 编辑信息
            editorinfo:{
                name:'',
                desc:'',
            },
            // 标签树
            tagtree:null,
            // 默认展开项 key 值数组
            tags_extended:null,
            // 默认选中项 key值数组
            tags_checked:null,
        };
    },
    created(){

    },
    mounted(){
        this.oninit()
    },
    //props:{},//[]
    methods:{
        async gettagtree(cb){
            let {data:res}=await this.$http.get(
                `/rights/tree`,{}
            )
            this.tagtree=res.data.map((m,mi)=>{
                m.label=m.authName;
                m.children=m.children.map((l,li)=>{
                    l.label=l.authName;
                    l.children=l.children.map((o,oi)=>{
                        o.label=o.authName;
                        return o;
                    })
                    return l;
                })
                return m;
            })
            console.log('标签树',this.tagtree)
            this.tags_extended=this.extend_flex(this.key_exist(this.tagtree))
            cb()
        },
        async oninit(){
            this.r=null;
            this.newerinfo=this.editorinfo=null
            this.newerinfo={
                name:'',
                desc:''
            };
            this.editorinfo={
                name:'',
                desc:''
            }
            let {data:res}=await this.$http.get(
                `/roles`,{params:{}}
            )
            this.role_list=res.data
            // console.log('初始化页面数据,获取角色列表', this.role_list)
            console.group('role_level 角色信息列表 其中单项')
                console.log(this.role_list[0])
                if(this.role_list[0].children[0]){
                console.group('manage_level 主标签-管理标签列表 其中单项')
                    console.log(this.role_list[0].children[0])
                    if(this.role_list[0].children[0].children[0]){
                    console.group('list_level 次级标签-分类列表标签列表 其中单项')
                        console.log(this.role_list[0].children[0].children[0])
                        if(this.role_list[0].children[0].children[0].children[0]){
                        console.group('edit_level 修改标签列表 其中单项')
                            console.log(this.role_list[0].children[0].children[0].children[0])
                        console.groupEnd()
                        }
                    console.groupEnd()
                    }
                console.groupEnd()
                }
            console.groupEnd()
        },
        // 递归获取key值列表
        // 过滤多维数组，得到key值多维数组
        key_exist(k){
            if(k.children){
                // 真实输出项构建
                // 输出一个数组
                return k.children.map((m,mi)=>{
                    return this.key_exist(m)
                })
            }else{
                // 递归终结分支
                return k.id
            }
        },
        // 递归多维数组变为简单数组
        extend_flex(flex){
            if(!flex){
                console.log('这尼玛啥也没有')
                return flex
            }
            let fi=flex.findIndex(m=>{return typeof(m)=='object'});
            if(fi!=-1){
                let fm=flex[fi]
                flex.splice(fi,1,...fm)
                return this.extend_flex(flex)
            }else{
                return flex
            }
        },
        // 开关
        onfatechange(r,k){
            this.r=r
            console.log('当前操作行信息对象',r)
            switch(k){
                case 'edit':
                    this.editorinfo.name=r.roleName
                    this.editorinfo.desc=r.roleDesc
                    this.show_edit_board=true
                break;
                case 'del':
                    this.show_del_board=true
                break;
                case 'set':
                    let flex=this.key_exist(r)
                    this.tags_checked=this.extend_flex(flex)
                    console.log('默认选中标签数组',this.tags_checked)
                    this.gettagtree(()=>{
                        this.show_role_board=true
                    })
                break;
            }
        },
        // 权限树 选中变化 触发
        when_marked_change(node_data,node_ismark,tree_hasmark){
            if(node_data.children){
                return
            }
            console.log('权限树选中状态改变',node_data,node_ismark,tree_hasmark)
            console.log('临刑前查看',this.tags_checked)
            if(node_ismark){
                this.tags_checked.push(node_data.id)
            }else{
                let fi=this.tags_checked.findIndex(m=>{return m==node_data.id})
                this.tags_checked.splice(fi,1)
            }
        },
        // 添加
        async waytonew(){
            let {data:res}=await this.$http.post(
                `/roles`,{
                    roleName:this.newerinfo.name,
                    roleDesc:this.newerinfo.desc,
                }
            )
            console.log(res)
            this.open_role_adder=false
            this.r=null
            this.oninit()
        },
        // 修改
        async waytoedit(r){
            let {data:res}=await this.$http.put(
                `/roles/${r.id}`,{
                    roleName:this.editorinfo.name,
                    roleDesc:this.editorinfo.desc,
                }
            )
            console.log(res)
            this.show_edit_board=false
            this.r=null
            this.oninit()
        },
        // 删除
        async waytodel(r){
            let {data:res}=await this.$http.delete(
                `/roles/${r.id}`,{}
            )
            console.log(res)
            this.show_del_board=false
            this.r=null
            this.oninit()
        },
        // 分配
        async waytorole(r){
            console.log('临行检查',this.tags_checked)
            let {data:res}=await this.$http.post(
                `/roles/${r.id}/rights`,{
                    rids:this.tags_checked.join(',')
                }
            )
            console.log(res)
            this.show_role_board=false
            this.r=null
            this.tags_checked=null
            this.oninit()
        },
    },
    computed:{

    },
    watch:{

    },
    filters:{

    },
    directives:{

    },
    components:{

    },
};
</script>